@tailwind base;
@tailwind components;
@tailwind utilities;
*, *:before, *:after {
  box-sizing: border-box;
}

input, textarea {
  padding: 9px;
  border: solid 1px #E5E5E5;
  outline: 0;
  font: normal 13px/100% Verdana, Tahoma, sans-serif;
  width: 100%;
  background: #FFFFFF;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

textarea {
  width: 100%;
  height: 150px;
  line-height: 150%;
}

input:hover, textarea:hover,
input:focus, textarea:focus {
  border-color: #C9C9C9;
}

button, input[type=submit] {
  width: auto;
  padding: 9px 15px;
  background: #cccccc;
  border: 0;
  font-size: 14px;
  color: #FFFFFF;
}